.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.dragContainer {
  display: flex;
}

.dropZone {
  flex-basis: 100%;
  flex-grow: 1;
  margin: 10px;
  padding: 10px;
  border: 1px solid gray;
}

.draggable {
  background-color: aquamarine;
  font-weight: normal;
  margin-bottom: 10px;
  margin-top: 10px;
  padding: 10px;
  display: inline-block;
}

.scrollContainer {
  height: 150px;
  width: 500px;
  overflow: auto;
}

.scrollContainer > div {
  height: 800px;
}
